import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { clients, clientPricing } from '../dashboards/data';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-client-detail',
  templateUrl: './client-detail.component.html',
  styleUrls: ['./client-detail.component.scss']
})
export class ClientDetailComponent implements OnInit {

  client: any;
  breadCrumbItems!: Array<{}>;
  clientForm!: FormGroup;
  isClientRemove: boolean = false;
  tempPricingItems: any;
  clientPricingItems: any[] =[];
  priceItemForm!: FormGroup;
  isNewPrice: boolean = false;

  constructor(
    private route: ActivatedRoute,
    private modalService: NgbModal,
    private formBuilder: FormBuilder,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.route.paramMap.subscribe(params => {
      let id = params.get('id');
      this.client = clients.find(x => x.id == id);
      this.tempPricingItems = clientPricing;
      this.clientPricingItems = this.tempPricingItems[this.client.id];
    });

    this.breadCrumbItems = [
      { label: 'Clients' },
      { label: 'Client', active: true }
    ];
  }

  onSubmit() {
    if (!this.isClientRemove) {
      if (this.clientForm.invalid) {
        return;
      } else {

        let client = {
          id: this.clientForm.value.id,
          name: this.clientForm.value.name,
          email: this.clientForm.value.email,
          phone: this.clientForm.value.phone,
          address: this.clientForm.value.address,
        }
        let index = clients.findIndex(x => x.id == this.clientForm.value.id);
        if (index != -1) {
          clients[index] = client;
          this.client = { ...client };
        }
      }
    } else {
      let index = clients.findIndex(x => x.id == this.client.id);
      if (index != -1) {
        clients.splice(index, 1);
        this.goBackToClients();
      }
    }
    this.modalService.dismissAll();
  }

  openModal(content: any, isClientRemove: boolean) {
    this.isClientRemove = isClientRemove;
    this.buildForm();
    this.modalService.open(content, { windowClass: 'modal-holder' });
  }

  openPricingModal(content: any, code: string = '') {
    this.isNewPrice = code == '';
    this.buildPriceForm(code);
    this.modalService.open(content, { windowClass: 'modal-holder' });
  }

  buildPriceForm(code: string) {
    if (code == '') {
      this.priceItemForm = this.formBuilder.group({
        code: [''],
        type: ['', [Validators.required]],
        taskName: ['', [Validators.required]],
        uom: ['', [Validators.required]],
        pricing: ['', [Validators.required]],
      });
    } else {
      let items: any[] = this.tempPricingItems[this.client.id];
      let pricing = items.find(x => x.code == code);
      this.priceItemForm = this.formBuilder.group({
        code: [{ value: pricing.code, disabled: true }],
        type: [pricing.type, [Validators.required]],
        taskName: [pricing.taskName, [Validators.required]],
        uom: [pricing.uom, [Validators.required]],
        pricing: [pricing.pricing, [Validators.required]],
      });
    }
  }

  onSubmitPricing() {
    if (this.priceItemForm.invalid) {
      return;
    } else {
      let form = this.priceItemForm.getRawValue();
      let newPrice = {
        code: form.code,
        type: this.priceItemForm.value.type,
        taskName: this.priceItemForm.value.taskName,
        uom: this.priceItemForm.value.uom,
        pricing: this.priceItemForm.value.pricing,
      }

      if (this.isNewPrice) {
        this.tempPricingItems[this.client.id].push(newPrice);
      } else {
        let temp = this.tempPricingItems[this.client.id];
        for (let i = 0; i <= temp.length; i++) {
          if (temp[i].code == form.code) {
            temp[i] = newPrice;
            break;
          }
        }
      }
    }
    this.modalService.dismissAll();
  }

  buildForm() {
    if (this.isClientRemove) {
      this.clientForm = this.formBuilder.group({
        id: [''],
        name: [''],
        phone: [''],
        address: [''],
        email: [''],
      });
    } else {
      this.clientForm = this.formBuilder.group({
        id: [this.client.id],
        name: [this.client.name, [Validators.required]],
        phone: [this.client.phone, [Validators.required]],
        address: [this.client.address, [Validators.required]],
        email: [this.client.email, [Validators.required, Validators.email]],
      });
    }
  }

  goBackToClients(): void {
    this.router.navigate(['/clients']);
  }
}
