import { Component, OnInit, ViewChild } from '@angular/core';
import { transactions, orders, clients, clientPricing, inspectionTypes, users, productions } from './data';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Select2Data } from 'ng-select2-component';
import { Router } from '@angular/router';

@Component({
  selector: 'app-dashboards',
  templateUrl: './dashboards.component.html',
  styleUrls: ['./dashboards.component.scss']
})

/**
 * Dashboard Component
 */
export class DashboardsComponent implements OnInit {

  // bread crumb items
  breadCrumbItems!: Array<{}>;
  transactions: any;
  orders: any;
  productionForm!: FormGroup;
  isNewProduction: boolean = false;
  currentClients: any[] = [];
  inspectionTypes: any[] = [];
  users: any[] = [];
  data!: Select2Data;
  tempPricingItems: any;
  clientPricingItems: any[] = [];
  productions: any[] = [];


  get f() { return this.productionForm.controls; }

  @ViewChild('content') content: any;

  constructor(
    private modalService: NgbModal,
    private formBuilder: FormBuilder,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.breadCrumbItems = [
      { label: 'Dashboard' },
      { label: 'Dashboard', active: true }
    ];
    this.fetchData();
  }

  mapUsersToSelect2Data(){
    let tempList: any[] = [];
    this.users.forEach((user) => {
      user.options =  JSON.parse(JSON.stringify(this.clientPricingItems));
      let newData = {
        value: user, 
        label: user.firstName + " " + user.lastName, 
      }
      tempList.push(newData);
    });
    this.data = tempList;
  }

  private fetchData() {
    this.transactions = transactions;
    this.orders = orders;
    this.currentClients = clients;
    this.inspectionTypes = inspectionTypes;
    this.users = users;
    this.tempPricingItems = clientPricing;
    this.productions = productions;
  }

  

  openModal(content: any, productionId: string = '') {
    this.isNewProduction = productionId == '';
    this.buildForm();
    this.modalService.open(content, { windowClass: 'modal-holder' });
    this.productionForm?.get('clientId')?.valueChanges.subscribe(clientId => {
      let options = this.tempPricingItems[clientId];
      this.productionForm?.get('options')?.setValue(options);
      this.clientPricingItems = options;
      let client = this.currentClients.find(x=>x.id == clientId);
      this.productionForm?.get('client')?.setValue(client);
      this.mapUsersToSelect2Data();
    });
  }

  buildForm(userId: string = '') {
    this.productionForm = this.formBuilder.group({
       id:[Math.floor(Math.random() * 900) + 100],
       startDate: [''],
       endDate: [''],
       clientId: [''],
       client:[''],
       goodWeatherDay: [''],
       inspectionType: [''],
       zone: [''],
       poNumber: [''],
       users: [''],
       options: [''],
    });
  }

  onSubmit() {
    
    if (this.productionForm.invalid) return;

    let formData = this.productionForm.getRawValue();
    productions.push(formData);

    //   if (this.productionForm.invalid) {
    //     return;
    //   } else {
    //     let user = {
    //       id: this.isNewUser ? "#SD" + Math.floor(Math.random() * 900) + 100 : this.userForm.value.id,
    //       index: Math.floor(Math.random() * 100) + 1,
    //       firstName: this.userForm.value.firstName,
    //       email: this.userForm.value.email,
    //       lastName: this.userForm.value.lastName,
    //       permission: this.userForm.value.permission,
    //       payRate: this.userForm.value.payRate,
    //     }
    //     if (this.isNewUser) {
    //       users.push(user);
    //     } else {
    //       let index =  users.findIndex(x=>x.id == this.userForm.value.id);
    //       if (index != -1) {
    //         users[index] = user;
    //       }
    //     }
    //     this.modalService.dismissAll();
    //   }
    this.modalService.dismissAll();
    this.openProductionDetails(formData.id);
  }

  openProductionDetails(productionId: string): void {
    this.router.navigate(['/production/', productionId]);
  }
}
