import { Component, OnInit } from '@angular/core';
import { clients, clientPricing, inspectionTypes, users, productions } from '../dashboards/data';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-production-detail',
  templateUrl: './production-detail.component.html',
  styleUrls: ['./production-detail.component.scss']
})
export class ProductionDetailComponent implements OnInit {

  breadCrumbItems!: Array<{}>;
  production: any;

   constructor(
    private route: ActivatedRoute,
    private router: Router
  ) { }
 
   ngOnInit(): void {
     this.breadCrumbItems = [
       { label: 'Productions' },
       { label: 'Production', active: true }
     ];

     this.route.paramMap.subscribe(params => {
       let id = params.get('id');
       let prods: any[] = productions;
       this.production = prods.find(x => x.id == id);
       if(!this.production) this.router.navigate(['']);
     });
   }

  calculateTotals(code: string) {
    let total = 0;
    this.production.users.forEach((user: { options: any[]; }) => {

      user.options.forEach((option) => {
        if (option.code == code && option.value != '') {
          total += Number(option.value);
        }
      });
    });
    return total;
  }
}
