import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { clients } from 'src/app/pages/dashboards/data';

@Component({
  selector: 'app-client',
  templateUrl: './client.component.html',
  styleUrls: ['./client.component.scss']
})
export class ClientComponent implements OnInit {

  @Input() clients: any[] = [];
  
  isNewClient: boolean = false;
  clientForm!: FormGroup;

  get f() { return this.clientForm.controls; }

  constructor(
    private modalService: NgbModal,
    private formBuilder: FormBuilder,
    private router: Router
  ) { }

  ngOnInit(): void {
  }

  
  openModal(content: any, userId: string = '') {
    this.buildForm(userId);
    this.isNewClient = userId == '' ? true : false;
    this.modalService.open(content, { windowClass: 'modal-holder' });
  }

  buildForm(clientId: string = '') {
    this.clientForm = this.formBuilder.group({
      id:[''],
      name: ['', [Validators.required]],
      phone: ['', [Validators.required]],
      address: ['', [Validators.required]],
      email: ['', [Validators.required, Validators.email]],
    });

    if (clientId != '') {
      let client = this.clients?.find(x => x.id == clientId);
      if (client) {
        this.clientForm.get("id")?.setValue(client.id)
        this.clientForm.get("name")?.setValue(client.name)
        this.clientForm.get("phone")?.setValue(client.phone)
        this.clientForm.get("address")?.setValue(client.address)
        this.clientForm.get("email")?.setValue(client.email)
      }
    }
  }

  onSubmit() {
    if (this.clientForm.invalid) {
      return;
    } else {
      let client = {
        id: this.isNewClient ? "#SD" + Math.floor(Math.random() * 900) + 100 : this.clientForm.value.id,
        name: this.clientForm.value.name,
        email: this.clientForm.value.email,
        phone: this.clientForm.value.phone,
        address: this.clientForm.value.address,
      }
      if (this.isNewClient) {
        clients.push(client);
      } else {
        let index =  clients.findIndex(x=> x.id == this.clientForm.value.id);
        if (index != -1) {
          clients[index] = client;
        }
      }
      this.modalService.dismissAll();
    }
  }

  openClientDetails(clientId: string): void {
    this.router.navigate(['/client/', clientId]);
  }
}
