import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { users } from 'src/app/pages/dashboards/data'

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss']
})

/**
 * User Component
 */
export class UserComponent implements OnInit {

  @Input() users: any[] = [];
  
  isNewUser: boolean = false;
  userForm!: FormGroup;

  get f() { return this.userForm.controls; }

  constructor(
    private modalService: NgbModal,
    private formBuilder: FormBuilder
  ) { }

  ngOnInit(): void {
  }

  openModal(content: any, userId: string = '') {
    this.buildForm(userId);
    this.isNewUser = userId == '' ? true : false;
    this.modalService.open(content, { windowClass: 'modal-holder' });
  }

  buildForm(userId: string = '') {
    this.userForm = this.formBuilder.group({
      id:[''],
      email: ['', [Validators.required, Validators.email]],
      firstName: ['', [Validators.required]],
      lastName: ['', [Validators.required]],
      payRate: ['', [Validators.required]],
      permission: ['None', [Validators.required]],
    });

    if (userId != '') {
      let user = this.users?.find(x => x.id == userId);
      if (user) {
        this.userForm.get("id")?.setValue(user.id)
        this.userForm.get("email")?.setValue(user.email)
        this.userForm.get("lastName")?.setValue(user.lastName)
        this.userForm.get("firstName")?.setValue(user.firstName)
        this.userForm.get("payRate")?.setValue(user.payRate)
        this.userForm.get("permission")?.setValue(user.permission)
      }
    }
  }

  onSubmit() {
    // stop here if form is invalid
    if (this.userForm.invalid) {
      return;
    } else {
      let user = {
        id: this.isNewUser ? "#SD" + Math.floor(Math.random() * 900) + 100 : this.userForm.value.id,
        index: Math.floor(Math.random() * 100) + 1,
        firstName: this.userForm.value.firstName,
        email: this.userForm.value.email,
        lastName: this.userForm.value.lastName,
        permission: this.userForm.value.permission,
        payRate: this.userForm.value.payRate,
        options: []
      }
      if (this.isNewUser) {
        users.push(user);
      } else {
        let index =  users.findIndex(x=>x.id == this.userForm.value.id);
        if (index != -1) {
          users[index] = user;
        }
      }
      this.modalService.dismissAll();
    }
  }
}
